import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'General',
  },
  {
    path: 'Buttons',
    breadcrumbName: 'Buttons',
  },
];

const Buttons = () => {
  return (
    <>
      <PageHeader title="Button-color" breadcrumb={{ routes }} subTitle="Buttons" style={{ padding: 10 }} />

      <div className="MF_area">
        <button type="submit" className="btn btn_default">btn_default</button>&nbsp;
        <button type="submit" className="btn btn_gray">btn_gray</button>&nbsp;
        <button type="submit" className="btn btn_blue">btn_blue</button>&nbsp;
        <button type="submit" className="btn btn_green">btn_green</button>&nbsp;
        <button type="submit" className="btn btn_skyblue">btn_skyblue</button>&nbsp;
        <button type="submit" className="btn btn_orange">btn_orange</button>&nbsp;
        <button type="submit" className="btn btn_red">btn_red</button>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<button type="submit" class="btn">btn_default</button>
<button type="submit" class="btn btn_gray">btn_gray</button>
<button type="submit" class="btn btn_blue">btn_blue</button>
<button type="submit" class="btn btn_green">btn_green</button>
<button type="submit" class="btn btn_skyblue">btn_skyblue</button>
<button type="submit" class="btn btn_orange">btn_orange</button>
<button type="submit" class="btn btn_red">btn_red</button>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


      <PageHeader title="Button-disabled" breadcrumb={{ routes }} subTitle="Buttons" style={{ padding: 10 }} />

      <div className="MF_area">
        <button type="submit" className="btn btn_default" disabled>btn_default</button>&nbsp;
        <button type="submit" className="btn btn_gray" disabled>btn_gray</button>&nbsp;
        <button type="submit" className="btn btn_blue" disabled>btn_blue</button>&nbsp;
        <button type="submit" className="btn btn_green" disabled>btn_green</button>&nbsp;
        <button type="submit" className="btn btn_skyblue" disabled>btn_skyblue</button>&nbsp;
        <button type="submit" className="btn btn_orange" disabled>btn_orange</button>&nbsp;
        <button type="submit" className="btn btn_red" disabled>btn_red</button>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<button type="submit" class="btn">btn_default</button>
<button type="submit" class="btn btn_gray">btn_gray</button>
<button type="submit" class="btn btn_blue">btn_blue</button>
<button type="submit" class="btn btn_green">btn_green</button>
<button type="submit" class="btn btn_skyblue">btn_skyblue</button>
<button type="submit" class="btn btn_orange">btn_orange</button>
<button type="submit" class="btn btn_red">btn_red</button>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


      <PageHeader title="Buttons-size" breadcrumb={{ routes }} subTitle="Buttons" style={{ padding: 10 }} />

      <div className="MF_area">
        <button type="submit" className="btn btn_blue btn_sm v_top">btn_sm</button>&nbsp;
        <button type="submit" className="btn btn_blue btn_md v_top">btn_md</button>&nbsp;
        <button type="submit" className="btn btn_blue btn_lg v_top">btn_lg</button>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<button type="submit" class="btn btn_blue btn_sm v_top">btn_sm</button>
<button type="submit" class="btn btn_blue btn_md v_top">btn_md</button>
<button type="submit" class="btn btn_blue btn_lg v_top">btn_lg</button>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>

      <PageHeader title="Buttons-icon" breadcrumb={{ routes }} subTitle="Buttons" style={{ padding: 10 }} />

      <div className="MF_area">
        <button type="submit" className="btn btn_window">window</button>&nbsp;
        <button type="submit" className="btn btn_android">android</button>&nbsp;
        <button type="submit" className="btn btn_apple">apple</button>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<button type="submit" class="btn btn_window">window</button>
<button type="submit" class="btn btn_android">android</button>
<button type="submit" class="btn btn_apple">apple</button>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
    </>
  );
}

export default Buttons;