import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Forms',
  },
  {
    path: 'CheckBoxs',
    breadcrumbName: 'CheckBoxs',
  },
];

const CheckBoxs = () => {
  return (
    <>

      <PageHeader title="CheckBox" breadcrumb={{ routes }} subTitle="CheckBox" style={{ padding: 10 }} />

      <div className="MF_area">
        <div className="checkbox_wrap">
          <input type="checkbox" id="checkbox" />
          <label htmlFor="checkbox">
            <div className="checkbox_style"><span></span></div>
            Checkbox는 이미지가 아닌 css로 작성됩니다.
          </label>
        </div>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="checkbox_wrap">
  <input type="checkbox" id="checkbox" />
  <label for="checkbox">
    <div class="checkbox_style"><span></span></div>
    Checkbox는 이미지가 아닌 css로 작성됩니다.
  </label>
</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
      <div className="MF_area">
        <div className="checkbox_wrap">
          <input type="checkbox" id="checkbox2" />
          <label htmlFor="checkbox2" style={{ marginRight: 10 }}>
            <div className="checkbox_style"><span></span></div>
            여러개를 사용하고 싶으시면 id 값을 다르게 주시면 됩니다.
          </label>

          <input type="checkbox" id="checkbox3" />
          <label htmlFor="checkbox3">
            <div className="checkbox_style"><span></span></div>
            IE9버전에서도 정상동작 하네요
          </label>
        </div>

      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="checkbox_wrap">
  <input type="checkbox" id="checkbox2" />
  <label for="checkbox2">
    <div class="checkbox_style"><span></span></div>
    여러개를 사용하고 싶으시면 id 값을 다르게 주시면 됩니다.
  </label>

  <input type="checkbox" id="checkbox3" />
  <label for="checkbox3">
    <div class="checkbox_style"><span></span></div>
    IE9버전에서도 정상동작 하네요
  </label>
</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
    </>
  );
}

export default CheckBoxs;