import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Data Display',
  },
  {
    path: 'Displays',
    breadcrumbName: 'Displays',
  },
];

const Displays = () => {
  return (
    <>
      <PageHeader title="Displays" breadcrumb={{ routes }} subTitle="Display-inline" style={{ padding: 10 }} />

      <div className="MF_area">
        <div className="dp_inline box">dp_inline</div>
        <div className="dp_inline box">dp_inline</div>
        <div className="dp_inline box">dp_inline</div>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="dp_inline box">dp_inline</div>
<div class="dp_inline box">dp_inline</div>
<div class="dp_inline box">dp_inline</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


      <PageHeader title="Displays" breadcrumb={{ routes }} subTitle="Display-block" style={{ padding: 10 }} />

      <div className="MF_area">
        <div className="dp_block box">dp_block</div>
        <div className="dp_block box">dp_block</div>
        <div className="dp_block box">dp_block</div>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="dp_block box">dp_block</div>
<div class="dp_block box">dp_block</div>
<div class="dp_block box">dp_block</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


      <PageHeader title="Displays" breadcrumb={{ routes }} subTitle="Display-Values" style={{ padding: 10 }} />

      <div className="MF_area">
        <ul>
          <li>dp_{`{value}`}</li>
        </ul>
        <ul>
          <li>none</li>
          <li>inline</li>
          <li>inline_b</li>
          <li>block</li>
          <li>table</li>
          <li>table_r</li>
          <li>table_c</li>
        </ul>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<ul>
    <li>dp_{value}</li>
</ul>
<ul>
    <li>none</li>
    <li>inline</li>
    <li>inline_b</li>
    <li>block</li>
    <li>table</li>
    <li>table_r</li>
    <li>table_c</li>
</ul>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
    </>
  );
}

export default Displays;