class Layer {
  constructor(el, opts) {
    this.el = el
    const layer = this.el.getAttribute('data-target')
    this.layer = document.querySelector(layer)

    const defaults = {
      mask: true,
      center: true
    }

    if (typeof opts !== 'undefined') {
      opts.forEach((i, v) => {
        if (defaults[i] !== undefined) {
          defaults[i] = v
        }
      })
    }

    this.options = defaults

    this.makeAction()
  }

  makeAction() {
    this.el.addEventListener('click', () => {
      if (this.options.mask === true) {
        Layer.maskControl(true)
      }

      this.layer.classList.add('show')
    })

    this.layer.querySelector('[data-label="close"]').addEventListener('click', () => {
      if (this.options.mask === true) {
        Layer.maskControl(false)
      }

      const layer = this.el.getAttribute('data-target')
      document.querySelector(layer).classList.remove('show')
    })
  }

  static maskControl(boolean) {
    if (boolean) {
      document.body.classList.add('layer_open')
    } else {
      document.body.classList.remove('layer_open')
    }
  }
}

