import React, { useState } from 'react';
import { Link, Route } from 'react-router-dom';
import { Layout, Menu, Icon } from 'antd';


// home
import GettingStarted from '../pages/GettingStarted';

// Feedback
import Notification from '../pages/Notification';
import Modal from '../pages/Modal';

// Data Display
import Carousel from '../pages/Carousel';
import Accordions from '../pages/Accordions';
import Displays from '../pages/Displays';
import List from '../pages/List';

// Data Entry
import AutoComplete from '../pages/AutoComplete';
import FormControl from '../pages/FormControl';
import Tables from '../pages/Tables';
import Texts from '../pages/Texts';
import VerticalAlignment from '../pages/VerticalAlignment';

// General
import Buttons from '../pages/Buttons';
import ListItemLabel from '../pages/ListItemLabel';

// Components
import Tabs_Component from '../pages/Tabs_Component';
import Tooltip_Component from '../pages/Tooltip_Component';
import SelectBox_Component from '../pages/SelectBox_Component';

// Layout
import Navigations from '../pages/Navigations';
import Floats from '../pages/Floats';
import Positions from '../pages/Positions';
import Spacings from '../pages/Spacings';


// Forms
import Inputs from '../pages/Inputs';
import Search from '../pages/Search';
import CheckBoxs from '../pages/CheckBoxs';
import Radios from '../pages/Radios';


const { SubMenu } = Menu;
const { Header, Content, Footer, Sider } = Layout;



const LayoutComponent = () => {
  const [openKeys, setOpenKeys] = useState(['sub1', 'sub2']);
  const onOpenChange = changeKeys => setOpenKeys(changeKeys);
  return (
    <>
      <Layout>
        <Sider breakpoint="lg" collapsedWidth="0" style={{ background: '#fff', minHeight: '100vh' }}>
          <Menu mode="inline" defaultSelectedKeys={['1']} openKeys={openKeys} onOpenChange={onOpenChange}>
            <Menu.Item key="1">
              <Icon type="home" />
              <span>
                <Link to="/GettingStarted">Getting Started</Link>
              </span>
            </Menu.Item>

            <SubMenu key="sub1" title={<span> <Icon type="edit" /> JavaScript </span>}>
              <Menu.Item key="2"><Link to="/AutoComplete">AutoComplete</Link></Menu.Item>
              <Menu.Item key="3"><Link to="/FormControl">FormControl</Link></Menu.Item>
              <Menu.Item key="4"><Link to="/Carousel">Carousel</Link></Menu.Item>
              <Menu.Item key="5"><Link to="/Modal">Modal</Link></Menu.Item>
              <Menu.Item key="6"><Link to="/Tabs_Component">Tabs</Link></Menu.Item>
              <Menu.Item key="7"><Link to="/Tooltip_Component">Tooltip</Link></Menu.Item>
              <Menu.Item key="8"><Link to="/SelectBox_Component">SelectBox</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="sub2" title={<span> <Icon type="desktop" /> CSS </span>}>
              <Menu.Item key="9"><Link to="/Accordions">Accordions</Link></Menu.Item>
              <Menu.Item key="10"><Link to="/ListItemLabel">ListItemLabel</Link></Menu.Item>
              <Menu.Item key="11"><Link to="/Buttons">Buttons</Link></Menu.Item>
              <Menu.Item key="12"><Link to="/Notification">Notification</Link></Menu.Item>
              <Menu.Item key="13"><Link to="/Navigations">Navigations</Link></Menu.Item>
              <Menu.Item key="14"><Link to="/Inputs">Inputs</Link></Menu.Item>
              <Menu.Item key="15"><Link to="/Search">Search</Link></Menu.Item>
              <Menu.Item key="16"><Link to="/CheckBoxs">CheckBox</Link></Menu.Item>
              <Menu.Item key="17"><Link to="/Radios">Radios</Link></Menu.Item>
              <Menu.Item key="18"><Link to="/Tables">Tables</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="sub3" title={<span> <Icon type="snippets" /> Declaration </span>}>
              <Menu.Item key="19"><Link to="/Spacings">Spacings</Link></Menu.Item>
              <Menu.Item key="20"><Link to="/Floats">Floats</Link></Menu.Item>
              <Menu.Item key="21"><Link to="/Positions">Positions</Link></Menu.Item>
              <Menu.Item key="22"><Link to="/Displays">Displays</Link></Menu.Item>
              <Menu.Item key="23"><Link to="/List">List</Link></Menu.Item>
              <Menu.Item key="24"><Link to="/Texts">Texts</Link></Menu.Item>
              <Menu.Item key="25"><Link to="/VerticalAlignment">VerticalAlignment</Link></Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
        <Layout>
          <Header style={{ background: '#fff', padding: 0 }} >
            <div className="Logo">Mania FrameWork</div>
          </Header>
          <Content style={{ margin: '24px 16px 0' }}>
            <div style={{ padding: 24, background: '#fff', minHeight: 100 }}>
              <Route exact path="/" component={GettingStarted}></Route>
              <Route path="/GettingStarted" component={GettingStarted}></Route>

              {/*General*/}
              <Route path="/ListItemLabel" component={ListItemLabel}></Route>
              <Route path="/Buttons" component={Buttons}></Route>

              {/*Alert*/}
              <Route path="/Notification" component={Notification}></Route>
              <Route path="/Modal" component={Modal}></Route>

              {/* Data Entry */}
              <Route path="/AutoComplete" component={AutoComplete}></Route>
              <Route path="/FormControl" component={FormControl}></Route>
              <Route path="/Tables" component={Tables}></Route>
              <Route path="/Texts" component={Texts}></Route>
              <Route path="/VerticalAlignment" component={VerticalAlignment}></Route>

              {/* Data Display */}
              <Route path="/Carousel" component={Carousel}></Route>
              <Route path="/Accordions" component={Accordions}></Route>
              <Route path="/Displays" component={Displays}></Route>
              <Route path="/List" component={List}></Route>

              {/* Components */}
              <Route path="/Tabs_Component" component={Tabs_Component}></Route>
              <Route path="/Tooltip_Component" component={Tooltip_Component}></Route>
              <Route path="/SelectBox_Component" component={SelectBox_Component}></Route>


              {/* Layout */}
              <Route path="/Navigations" component={Navigations}></Route>
              <Route path="/Floats" component={Floats}></Route>
              <Route path="/Positions" component={Positions}></Route>
              <Route path="/Spacings" component={Spacings}></Route>

              {/* Forms */}
              <Route path="/Inputs" component={Inputs}></Route>
              <Route path="/Search" component={Search}></Route>
              <Route path="/CheckBoxs" component={CheckBoxs}></Route>
              <Route path="/Radios" component={Radios}></Route>

              {/*Declaration*/}
              <Route path="/Spacings" component={Spacings}>Spacings</Route>
              <Route path="/Floats" component={Floats}>Floats</Route>
              <Route path="/Positions" component={Positions}>Positions</Route>
              <Route path="/Displays" component={Displays}>Displays</Route>
              <Route path="/List" component={List}>List</Route>
              <Route path="/Texts" component={Texts}>Texts</Route>
              <Route path="/VerticalAlignment" component={VerticalAlignment}>VerticalAlignment</Route>
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>IMI ©2019 Created by <b>FE</b> part</Footer>
        </Layout>
      </Layout>
    </>
  )
}

export default LayoutComponent;
