class Tooltip {
  constructor(el, opts) {
    // Object.extend(el, this);

    this.el = el
    const tooltip = this.el.getAttribute('data-target')
    this.tooltip = document.querySelector(tooltip)

    const defaults = {
      mask: true,
      center: true
    }

    if (typeof opts !== 'undefined') {
      opts.forEach((i, v) => {
        if (defaults[i] !== undefined) {
          defaults[i] = v
        }
      })
    }

    this.options = defaults

    this.makeAction()
  }

  makeAction() {
    this.el.addEventListener('mouseover', () => {
      this.tooltip.classList.add('show')
    })

    this.el.addEventListener('mouseout', () => {
      this.tooltip.classList.remove('show')
    })

    // this.tooltip.querySelector('[data-label="close"]').addEventListener('click', () => {
    //     if(this.options.mask === true) {
    //         this.maskControl(false);
    //     }
    //
    //     const tooltip = this.el.getAttribute('data-target')
    //     document.querySelector(tooltip).classList.remove('show')
    // })
  }

  static maskControl(boolean) {
    if (boolean) {
      document.body.classList.add('tooltip_open')
    } else {
      document.body.classList.remove('tooltip_open')
    }
  }
}

