class Tab {
  constructor(el) {
    this.el = el
    this.makeAction()
  }

  makeAction() {
    const ch = Array.prototype.slice.call(this.el.children)
    ch.forEach((el) => {
      el.addEventListener('click', () => {
        ch.forEach((t) => {
          t.classList.remove('selected')
        })

        const dpr = this.el.getAttribute('data-parent')
        if (dpr) {
          const pr = document.querySelector(dpr)
          const prch = pr.querySelectorAll('.tab_child')
          prch.forEach((el2) => {
            el2.classList.remove('show')
          })
        }

        const target = document.querySelector(el.getAttribute('data-target'))
        target.classList.add('show')
        el.classList.add('selected')
      })
    })
  }
}

