import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Data Display',
  },
  {
    path: 'Accordions',
    breadcrumbName: 'Accordions',
  },
];
const Accordions = () => {
  return (
    <>
      <PageHeader title="Accordions-FAQ" breadcrumb={{ routes }} subTitle="Accordions" style={{ padding: 10 }} />

      <div className="MF_area">
        <ul className="faq_contents_wrap">
          <li className="faq_title">FAQ</li>
          <li className="faq_content">
            <button>
              <div>컬쳐캐시(컬쳐랜드ID충전)이란?</div>
              <div>
                기존 PIN코드 입력의 불편함을 개선하여, 원하는 금액 결제 후<br />
                컬쳐랜드 ID입력을 통해 바로 충전을 제공하는 서비스 입니다.
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>사용 유효 기간이 있나요?</div>
              <div>
                핀 등록 후 사용 유효기간은 1년입니다.
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>등록 절차는 어떻게 되나요?</div>
              <div>
                수신 SMS 링크 확인 → 서비스약관동의 → 보안코드 입력 → 컬쳐랜드 ID입력 → 충전금액 및 아이디 확인 → 충전완료
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>구매내역은 어디서 확인하나요?</div>
              <div>
                [상품권몰] – [나의 구매내역] – [컬쳐캐시(컬쳐랜드ID충전)] 에서 확인 가능합니다.
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>모바일 발송 문의 안내</div>
              <div>
                최초 구매 신청시 1회 발송되며, 휴대폰번호 오기재로 인해 발생된 문제는 판매처가 책임 지지 않습니다.<br />
                ※ 핀번호가 오지 않을 경우<br />
                스팸문자함과 이통사 부가서비스 중 스팸 필터링, 스팸 차단서비스, 번호 도용 서비스를 이용 중 인지 확인바랍니다.<br />
                미 확인된 핀번호는 [상품권몰] – [나의 구매내역] – [컬쳐캐시(컬쳐랜드ID충전)] 에서 확인 가능합니다.<br />
                재발송 요청은 3회까지 가능하며, 요청은 (02-561-0671)로 문의 주시기 바랍니다.
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>컬쳐캐시(컬처랜드ID충전) 이용 문의 안내</div>
              <div>
                스마트콘 고객센터(02-561-0671)로 문의 주시길 바랍니다.
              </div>
            </button>
          </li>
        </ul>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<ul class="faq_contents_wrap">
  <li class="faq_title">FAQ</li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
</ul>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


    </>
  );
}

export default Accordions;