class Selectbox {
  constructor(el) {
    this.el = el

    const selectList = this.el.getAttribute('data-target')
    this.selectList = document.querySelector(selectList)

    this.makeAction()
  }

  makeAction() {
    window.addEventListener('click', (e) => {
      const bShow = this.selectList.classList.contains('show')

      if (bShow === true) {
        if (this.childList.indexOf(e.target) !== -1) {
          this.el.textContent = e.target.textContent
        }
        this.selectList.classList.remove('show')
      } else if (bShow === false && e.target === this.el) {
        this.selectList.classList.add('show')
      }
    })

    this.childList = []
    this.maxWidth = 0

    this.selectList.style.position = 'absolute'
    this.selectList.style.visibility = 'hidden'
    this.selectList.style.display = 'block'

    const ch = Array.prototype.slice.call(this.selectList.children)
    ch.forEach((el) => {
      el.addEventListener('click', (e) => {
        e.preventDefault()
      })
      this.childList.push(el)
      if (el.clientWidth > this.maxWidth) {
        this.maxWidth = el.clientWidth
      }
    })

    this.selectList.style.position = ''
    this.selectList.style.visibility = ''
    this.selectList.style.display = ''

    this.el.style.width = `${this.maxWidth + 40}px`
    this.selectList.style.minWidth = `${this.maxWidth + 40}px`
  }
}

