import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Forms',
  },
  {
    path: 'Radios',
    breadcrumbName: 'Radios',
  },
];

const Radios = () => {
  return (
    <>


      <PageHeader title="Radios" breadcrumb={{ routes }} subTitle="Radios" style={{ padding: 10 }} />

      <div className="MF_area">
        <div className="radiobox_wrap">
          <input type="radio" id="ok_radio" name="radio" />
          <label className="bold_text" htmlFor="ok_radio">
            <div className="radiobox_style"><span></span></div>
            네, 가입에 동의합니다.
        </label>
          <input type="radio" id="no_radio" name="radio" />
          <label className="bold_text" htmlFor="no_radio">
            <div className="radiobox_style"><span></span></div>
            아니오, 가입하지 않겠습니다.
        </label>
        </div>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="radiobox_wrap">
  <input type="radio" id="ok_radio" name="radio" checked />
  <label class="bold_text" for="ok_radio">
    <div class="radiobox_style"><span></span></div>
    네, 가입에 동의합니다.
  </label>
    <input type="radio" id="no_radio" name="radio" />
    <label class="bold_text" for="no_radio">
      <div class="radiobox_style"><span></span></div>
      아니오, 가입하지 않겠습니다.
  </label>
</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>





      <PageHeader title="Radios" breadcrumb={{ routes }} subTitle="Radios-multi" style={{ padding: 10 }} />

      <div className="MF_area">
        <div className="radiobox_wrap">
          <input type="radio" id="radio1" name="radios" />
          <label className="bold_text" htmlFor="radio1">
            <div className="radiobox_style"><span></span></div>
            Radio Button 01
          </label>
          <input type="radio" id="radio2" name="radios" />
          <label className="bold_text" htmlFor="radio2">
            <div className="radiobox_style"><span></span></div>
            Radio Button 02
        </label>
          <input type="radio" id="radio3" name="radios" />
          <label className="bold_text" htmlFor="radio3">
            <div className="radiobox_style"><span></span></div>
            Radio Button 03
          </label>
          <input type="radio" id="radio4" name="radios" />
          <label className="bold_text" htmlFor="radio4">
            <div className="radiobox_style"><span></span></div>
            Radio Button 04
          </label>
        </div>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="radiobox_wrap">
  <input type="radio" id="radio1" name="radios" />
  <label class="bold_text" for="radio1">
    <div class="radiobox_style"><span></span></div>
    Radio Button 01
  </label>
  <input type="radio" id="radio2" name="radios" />
  <label class="bold_text" for="radio2">
    <div class="radiobox_style"><span></span></div>
    Radio Button 02
  </label>
  <input type="radio" id="radio3" name="radios" />
  <label class="bold_text" for="radio3">
    <div class="radiobox_style"><span></span></div>
    Radio Button 03
  </label>
  <input type="radio" id="radio4" name="radios" />
  <label class="bold_text" for="radio4">
    <div class="radiobox_style"><span></span></div>
    Radio Button 04
  </label>
</div>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
    </>
  );
}

export default Radios;