class FormControl {
  constructor(f, opts) {
    this.form = f
    this.form.setAttribute('novalidate', true)

    this.defineRules()

    this.checkedEl = []
    this.validEl = []

    this.form.querySelectorAll('[required]').forEach((el) => {
      this.checkedEl.push(el)
    })

    this.form.querySelectorAll('[data-valid]').forEach((el) => {
      this.validEl.push(el)
      if (el.getAttribute('data-valid') === 'number') {
        el.addEventListener('keyup', () => {
          el.value = el.value.replace(/,/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')
        })
      }
    })

    this.form.addEventListener('submit', (e) => {
      e.preventDefault()
      this.checkSubmit();
    })
  }

  checkSubmit() {
    let checked = true
    this.checkedEl.some((el) => {
      if (FormControl.emptyCheck(el)) {
        checked = false
        const emptyMsg = (el.getAttribute('data-empty-msg')) ? el.getAttribute('data-empty-msg') : '값을 입력해주세요'
        alert(emptyMsg)
        el.focus()
      } else if (el.getAttribute('data-valid')) {
        const valid = el.getAttribute('data-valid')
        if (typeof this.rules[valid] === 'function') {
          let result
          if (valid === 'equalTo') {
            const equalEl = document.querySelector(el.getAttribute('valid-equalTo'))
            result = this.rules[valid].apply(el, [el.value, equalEl.value])
            checked = result
          } else {
            result = this.rules[valid].call(el, el.value)
            checked = result
          }

          if (result === false) {
            const msg = (el.getAttribute('data-msg')) ? el.getAttribute('data-msg') : this.msg[valid]
            alert(msg)
            el.focus()
          }
        }
      }
      return !checked
    })
    return checked
  }

  static emptyCheck(el) {
    if (el.value.trim() === '') {
      return true
    }
    return false
  }

  defineRules() {
    this.rules = {
      number(val) {
        const regx = /(\d)(?=(?:\d{3})+(?!\d))/
        if ((regx).test(val) === false) {
          const regexp = new RegExp(/[^0-9,]/, 'g')
          this.value = this.value.replace(regexp, '')
          return false
        }

        return true
      },
      digit(val) {
        const regx = /^\d+$/
        if ((regx).test(val) === false) {
          const regexp = new RegExp(/[^0-9]/, 'g')
          this.value = this.value.replace(regexp, '')
          return false
        }

        return true
      },
      equalTo(val, target) {
        return val === target.value
      },
      enNumber(val) {
        const regx = /[^a-zA-Z0-9]/
        if ((regx).test(val) === true) {
          const regexp = new RegExp(regx, 'g')
          this.value = this.value.replace(regexp, '')
          return false
        }

        return true
      },
      email(val) {
        const regx = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
        return (regx).test(val)
      },
      english(val) {
        const regx = /[^a-zA-Z]/
        if ((regx).test(val) === true) {
          const regexp = new RegExp(regx, 'g')
          this.value = this.value.replace(regexp, '')
          return false
        }

        return true
      },
      // password(val) {
      //   const regx = /(?=.*\d)(?=.*[a-zA-Z])/
      //   return (regx).test(val)
      // }
    }

    this.msg = {
      number: '숫자만 입력 가능합니다.',
      digit: '숫자만 입력 가능합니다.',
      equalTo: '일치하지 않습니다.',
      enNumber: '영문 및 숫자만 입력 가능합니다.',
      email: '이메일 형식으로 입력해주세요.',
      english: '영문만 입력 가능합니다.',
    }
  }
}

